import React, { Component } from "react";

class my404 extends Component {
  state = {
    movies: {},
  };

  render() {
    return (
      <div id="notfound">
        <div class="notfound">
          <div class="notfound-404">
            <h1>404</h1>
          </div>
          <h2>¡UPS! No se pudo encontrar esta página</h2>
          <p>
            Lo sentimos, pero la página que está buscando no existe, se ha
            eliminado. nombre cambiado o no está disponible temporalmente
          </p>
          <a href="/">Ir a la página de inicio</a>
        </div>
      </div>
    );
  }
}

export default my404;
