import React, { Component } from "react";
import {register, format} from 'timeago.js';

import LoadingImg from "./LoadingImg";

function removeDuplicates(originalArray, prop) {
  var newArray = [];
  var lookupObject = {};

  for (var i in originalArray) {
    lookupObject[originalArray[i][prop]] = originalArray[i];
  }

  for (i in lookupObject) {
    newArray.push(lookupObject[i]);
  }
  return newArray;
}

function moverseA(idDelElemento) {
  document.location.hash = "#" + idDelElemento;
}

function getOffsetLeft(elem) {
  var offsetLeft = 0;
  do {
    if (!isNaN(elem.offsetLeft)) {
      offsetLeft += elem.offsetLeft;
    }
  } while ((elem = elem.offsetParent));
  return offsetLeft;
}

function getOffsetTop(elem) {
  var offsetTop = 0;
  do {
    if (!isNaN(elem.offsetTop)) {
      offsetTop += elem.offsetTop;
    }
  } while ((elem = elem.offsetParent));
  return offsetTop;
}

function showDisqus() {
  var d = document,
    s = d.createElement("script");
  s.src = "https://cotiplustv.disqus.com/embed.js";
  s.setAttribute("data-timestamp", +new Date());
  (d.head || d.body).appendChild(s);
}

const serverDomain = "https://server-cotiplustv.herokuapp.com";

// const serverDomain = 'http://localhost:3001';


class WatchVideo extends Component {
  state = {
    videoInfo: {},
    videosWatch: [],
    videosRelevant: [],
    loading: true,
    newImage: "",
    video: "",
    languagesVideo: [],
    seasons: [],
    seasonsProx: -1,
    error: false,
    prevEpisode: "",
    nextEpisode: "",
    titleSearch: "",
    sizeWidth: 0
  };

  async componentDidMount() {
    const queryString = window.location.search;
    const titleSearch = window.location.href.split("/").pop().split("?")[0];
    // console.log(titleSearch);
    const urlParams = new URLSearchParams(queryString);
    const pageInit = urlParams.get("page") || 1;
    let response = await fetch(`${serverDomain}/ver/${titleSearch}`);
    let data = await response.json();


    let titlePageNew = document.querySelector("title");
    if (data.videoInfo) {
      titlePageNew.textContent =
        `Ver ${data.videoInfo.title}` + " | " + titlePageNew.textContent;
    }
    if (!data.error) {
      let newImage = data.videosWatch[0].image;
      let video;
      if (data.videoInfo.optionsStream.length) {
        video = data.videoInfo.optionsStream[0].stream;
      }
      let languagesVideo = removeDuplicates(
        data.videoInfo.optionsStream,
        "language"
      );
      // languagesVideo.forEach((item) => {
      //   if(item.language == 'Castellano') {
      //     item.language = 'Latino';
      //   } else if(item.language == 'Latino') {
      //     item.language = 'Castellano';
      //   }
      // });
      let seasons = removeDuplicates(data.videosWatch, "season");
      let prevEpisode = "";
      let nextEpisode = "";
      if (data.videoInfo.season) {
        let titleNewUrl = "";
        data.videosWatch.reverse();
        let indEpiNext = data.videosWatch.reverse().indexOf(data.videosWatch.reverse().find((t) => t.season == data.videoInfo.season && t.episode == data.videoInfo.episode));
        let indEpiPrev = data.videosWatch.indexOf(data.videosWatch.find((t) => t.season == data.videoInfo.season && t.episode == data.videoInfo.episode));
        // console.log(indEpi);
        // Verificar si existe capitulo anterior
        let seasonsPrev = data.videosWatch
          .reverse()
          .map((el) => el.season)
          .lastIndexOf(data.videoInfo.season - 1);
        if (data.videoInfo.episode > 1) {
          let indiceData = data.videoInfo.episode - 1;
          // while(true) {
          //   let dataObj = data.videosWatch.find((el) => el.season == data.videoInfo.season && el.episode == indiceData);
          //   if(dataObj) {
          //     break;
          //   } else {
          //     indiceData = indiceData - 1;
          //   }
          // }
          let especialTitle = '';
            // titleNewUrl = titleSearch.replace(
            //   `capitulo-${data.videoInfo.episode}`,
            //   `capitulo-${indiceData}`
            // ).replace(
            //   `episodio-${data.videoInfo.episode}`,
            //   `episodio-${indiceData}`
            // );
          titleNewUrl = data.videosWatch.reverse()[indEpiPrev - 1].targetWatch;
          prevEpisode = `${titleNewUrl}`;
        } else {
          if (seasonsPrev != -1) {
            // titleNewUrl = titleSearch
            //   .replace(
            //     `temporada-${data.videoInfo.season}`,
            //     `temporada-${data.videosWatch[seasonsPrev].season}`
            //   )
            //   .replace(
            //     `capitulo-${data.videoInfo.episode}`,
            //     `capitulo-${data.videosWatch[seasonsPrev].episode}`
            //   );
            titleNewUrl = data.videosWatch.reverse()[indEpiPrev - 1].targetWatch;
            prevEpisode = `${titleNewUrl}`;
          }
        }

        // Verificar si existe capitulo posterior
        let seasonsNumber = data.videosWatch.filter(
          (el) => el.season == data.videoInfo.season
        ).map((el) => el.episode);
        let numMaxEpisode = Math.max(...seasonsNumber);
        console.log(data.videoInfo.episode, seasonsNumber);
        let seasonsProx = data.videosWatch
          .map((el) => el.season)
          .indexOf(data.videoInfo.season + 1);
        if (data.videoInfo.episode < numMaxEpisode) {
          let indiceData = data.videoInfo.episode + 1;
          // while(true) {
          //   let dataObj = data.videosWatch.find((el) => el.season == data.videoInfo.season && el.episode == indiceData);
          //   if(dataObj) {
          //     break;
          //   } else {
          //     indiceData = indiceData + 1;
          //   }
          // }
          let especialTitle = '';
            // titleNewUrl = titleSearch.replace(
            //   `capitulo-${data.videoInfo.episode}`,
            //   `capitulo-${indiceData}`
            // ).replace(
            //   `episodio-${data.videoInfo.episode}`,
            //   `episodio-${indiceData}`
            // );
          if(data.videoInfo.episode == 1) {
            titleNewUrl = data.videosWatch.reverse()[indEpiNext + 1].targetWatch;
          } else {
            titleNewUrl = data.videosWatch[indEpiNext + 1].targetWatch;
          }
          nextEpisode = `${titleNewUrl}`;
        } else {
          if (seasonsProx != -1) {
            // titleNewUrl = titleSearch
            //   .replace(
            //     `temporada-${data.videoInfo.season}`,
            //     `temporada-${data.videosWatch[seasonsProx].season}`
            //   )
            //   .replace(
            //     `capitulo-${data.videoInfo.episode}`,
            //     `capitulo-${data.videosWatch[seasonsProx].episode}`
            //   );
            titleNewUrl = data.videosWatch[indEpiNext + 1].targetWatch;
            nextEpisode = `${titleNewUrl}`;
          }
        }
      }
      const localeFunc = (number, index, totalSec) => {
        // number: the timeago / timein number;
        // index: the index of array below;
        // totalSec: total seconds between date to be formatted and today's date;
        return [
          ['justo ahora', 'justo ahora'],
          ['%s segundos antes', 'en %s segundos'],
          ['1 minuto antes', 'en 1 minuto'],
          ['%s minutos antes', 'en %s minutos'],
          ['1 hora antes', 'en 1 hora'],
          ['%s horas antes', 'en %s horas'],
          ['1 día antes', 'en 1 día'],
          ['%s días antes', 'en %s días'],
          ['1 semana antes', 'en 1 semana'],
          ['%s semanas antes', 'en %s semanas'],
          ['1 mes antes', 'en 1 mes'],
          ['%s meses antes', 'en %s meses'],
          ['1 año antes', 'en 1 año'],
          ['%s años antes', 'en %s años']
        ][index];
      };
      // register your locale with timeago
      register('my-locale', localeFunc);
      let sizeWidth = window.innerWidth;
      this.setState({
        videoInfo: data.videoInfo,
        videosWatch: data.videosWatch,
        videosRelevant: data.videosRelevant,
        loading: false,
        newImage,
        video,
        languagesVideo,
        seasons,
        prevEpisode,
        nextEpisode,
        titleSearch,
        sizeWidth
      });
      LoadingImg();
      window.document.addEventListener("click", (e) => {
        if (!e.target.closest(".language__item") && !e.target.closest(".fa-angle-down")) {
          let selectedsActive = document.querySelectorAll(".options__stream");
          selectedsActive.forEach((item) => {
            item.classList.add("d-none");
            item.parentElement.querySelector(
              ".material-icons"
            ).innerHTML = `<i class="fas fa-angle-down"></i>`;
          });
        }
      });
      showDisqus();
    } else {
      this.setState({ error: true, loading: false });
    }
  }

  render() {
    return (
      <div className="watch__video__content">
        {this.state.loading ? (
          <div className="loading-page-skeletor">
            <div class="skeletor-video">
              <div class="skeletor-img">
                <div class="background-image"></div>
              </div>
              <div class="skeletor-info">
                <div class="line line-1"></div>
                <div class="line line-2"></div>
                <div class="line line-3"></div>
              </div>
            </div>
            <div class="skeletor-stream-video">
              <div class="skeletor-container-watch">
                <div class="skeletor-watch"></div>
              </div>
            </div>
          </div>
        ) : this.state.error ? (
          <div id="notfound">
            <div class="notfound">
              <div class="notfound-404">
                <h1>404</h1>
              </div>
              <h2>¡UPS! No se pudo encontrar esta página</h2>
              <p>
                Lo sentimos, pero la página que está buscando no existe, se ha
                eliminado. nombre cambiado o no está disponible temporalmente
              </p>
              <a href="/">Ir a la página de inicio</a>
            </div>
          </div>
        ) : (
          <div>
            <div className="card__preview">
              <div className="card__video">
                <div className="img__video__stream">
                  <img
                    className="image-loading"
                    src={this.props.imagePreloader}
                    alt=""
                    data-src={this.state.newImage}
                  />
                </div>
                <div className="card__info">
                  <div className="title__video">
                    {`${this.state.videoInfo.titleEpisode}`}
                  </div>
                  <div className="title__original">
                    @ {this.state.videoInfo.title}
                  </div>
                  <div className="title__indicator__description">Sinopsis:</div>
                  <div className="description__video">
                    {this.state.videoInfo.description}
                  </div>
                  <div className="country__video">
                    <div className="indicator">País:</div>
                  </div>
                  <div className="release__date__video">
                    <div className="indicator">Fecha:</div>
                  </div>
                </div>
              </div>
            </div>
            <div className="video-content">
              <div className="languages__video">
                {this.state.languagesVideo.map((item, z) => {
                  return (
                    <div className="language__options__content">
                      <div
                        className={
                          z == 0
                            ? "language__item selected__language"
                            : "language__item"
                        }
                        onClick={this.toggleOption.bind(this)}
                      >
                        <div class="_11wqw material-icons">
                          <i class="fas fa-angle-down"></i>
                        </div>
                        <div className="img__language">
                          <img
                            src={
                              item.language == "Subtitulado"
                                ? "/en.svg"
                                : item.language == "Castellano"
                                ? "/es-es.svg"
                                : item.language == "Latino"
                                ? "/es-mx.svg"
                                : ""
                            }
                          ></img>
                        </div>
                        <span className="title__language">{item.language}</span>
                        <div className="img__hd">
                          <img src="/hd.svg"></img>
                        </div>
                      </div>
                      <div className="options__stream d-none" id={`stream${z}`}>
                        <div className="indicator__stream">
                          Elige el servidor
                        </div>
                        {this.state.videoInfo.optionsStream.map((stream, i) => {
                          return i == 0 ? (
                            stream.language == item.language ? (
                              <div
                                onClick={this.changeStream.bind(this)}
                                className="option selected__option__stream"
                                data-video={stream.stream}
                              >
                                {stream.option}
                              </div>
                            ) : (
                              ""
                            )
                          ) : stream.language == item.language ? (
                            <div
                              onClick={this.changeStream.bind(this)}
                              className="option"
                              data-video={stream.stream}
                            >
                              {stream.option}
                            </div>
                          ) : (
                            ""
                          );
                        })}
                        <div className="extend__stream">
                          La diferencia principal entre estos es la velocidad de
                          carga
                        </div>
                      </div>
                    </div>
                  );
                })}
              </div>
              <div className="play-video">
                <iframe
                  src={this.state.video}
                  allowfullscreen="true"
                  frameborder="0"
                  marginwidth="0"
                  marginheight="0"
                  scrolling="no"
                ></iframe>
              </div>
              <div className="change__episode">
                {this.state.prevEpisode != "" ? (
                  this.state.sizeWidth <= 506 ? <a
                  href={this.state.prevEpisode}
                  class="action__episode prev__episode"
                >
                  <i class="fas fa-backward"></i>
                </a> :
                  <a
                    href={this.state.prevEpisode}
                    class="action__episode prev__episode"
                  >
                    Capitulo Anterior
                  </a>
                ) : (
                  this.state.sizeWidth <= 506 ? <a
                  href={"javascript:void(0);"}
                  class="action__episode prev__episode inactive__episode"
                >
                  <i class="fas fa-backward"></i>
                </a> :
                  <a
                    href={"javascript:void(0);"}
                    class="action__episode prev__episode inactive__episode"
                  >
                    Capitulo Anterior
                  </a>
                )}
                
                {this.state.videoInfo.titleEpisode.toLowerCase().includes('temporada') ?
                  this.state.sizeWidth <= 506 ? <a href={`/ver/${this.state.titleSearch}/temporadas`} className="action__episode all__episodes">
                  <i class="fas fa-bars"></i>
                </a> : <a href={`/ver/${this.state.titleSearch}/temporadas`} className="action__episode all__episodes">
                  Todos los capitulos
                </a> : ''}
                {this.state.nextEpisode != "" ? (
                  this.state.sizeWidth <= 506 ? <a
                  href={this.state.nextEpisode}
                  class="action__episode next__episode"
                >
                  <i class="fas fa-forward"></i>
                </a> :
                  <a
                    href={this.state.nextEpisode}
                    class="action__episode next__episode"
                  >
                    Capitulo Siguiente
                  </a>
                ) : (
                  this.state.sizeWidth <= 506 ? <a
                  href={"javascript:void(0);"}
                  class="action__episode next__episode inactive__episode"
                >
                  <i class="fas fa-forward"></i>
                </a> :
                  <a
                    href={"javascript:void(0);"}
                    class="action__episode next__episode inactive__episode"
                  >
                    Capitulo Siguiente
                  </a>
                )}
              </div>
            </div>
            <div id="disqus_thread"></div>
            <div className="video-info-extend">
              <h3 className="indicator__extend__info title_videos_last">
                Últimos estrenos
              </h3>
              <div className="content_img_videos animate__animated animate__fadeIn">
                {this.state.videosRelevant.map((movie) => {
                  return (
                    <a
                      href={movie.targetWatch}
                      className="carousel__elemento wave tooltip_auto"
                      data-tippy-content={movie.title}
                      data-video-id=""
                      key={movie.targetWatch}
                    >
                      <img
                        className="image-loading"
                        src={this.props.imagePreloader}
                        alt=""
                        data-src={movie.image}
                      />
                      <p className="title-video">{movie.title}</p>
                      <div className="history__date">
                        <label className="date">{movie.date.includes(':') ? format(movie.date, 'my-locale') : movie.date}</label>
                      </div>
                    </a>
                  );
                })}
              </div>
            </div>
            <a class="more__videos__content">
              {" "}
              <a class="btnMoreVideos wave" href="/">
                Ver más
              </a>{" "}
            </a>
          </div>
        )}
      </div>
    );
  }
  toggleOption(e) {
    let elementTarget = e.currentTarget.parentElement.querySelector(".options__stream");
    let optionsActive = document.querySelectorAll(".options__stream");
    optionsActive.forEach((t) => {
      if (t != elementTarget) t.classList.add("d-none");
    });
    // console.log(e.target.closest('.options__stream'));
    if (!e.target.closest(".options__stream")) {
      elementTarget.classList.toggle("d-none");
    }
    let selectedsActive = document.querySelectorAll(".options__stream");
    selectedsActive.forEach((item) => {
      if (item != elementTarget) item.classList.add("d-none");
      item.parentElement.querySelector(
        ".material-icons"
      ).innerHTML = `<i class="fas fa-angle-down"></i>`;
    });
    if (elementTarget) {
      if (elementTarget.classList.contains("d-none")) {
        // elementTarget.parentElement.classList.remove('selected__language');
        elementTarget.parentElement.querySelector(
          ".material-icons"
        ).innerHTML = `<i class="fas fa-angle-down"></i>`;
      } else {
        // elementTarget.parentElement.classList.add('selected__language');
        elementTarget.parentElement.querySelector(
          ".material-icons"
        ).innerHTML = `<i class="fas fa-angle-up"></i>`;
      }
    }
  }

  changeStream(e) {
    let selected = document.querySelector(".selected__option__stream");
    let thisTarget = e.currentTarget;
    if(selected)  {
      selected.classList.remove("selected__option__stream");
    }
    thisTarget.classList.add("selected__option__stream");
    let videoPlayer = document.querySelector(".play-video");
    videoPlayer.innerHTML = `<iframe src="${e.currentTarget.dataset.video}" allowfullscreen="true" frameborder="0" marginwidth="0" marginheight="0" scrolling="no"></iframe>`;
    let selected__language = document.querySelectorAll(".selected__language");
    selected__language.forEach((t) => {
      t.classList.remove("selected__language");
    });
    let selectedStream = thisTarget.parentElement.parentElement.querySelector('.language__item');
    selectedStream.classList.add("selected__language");
    let optionsActive = document.querySelectorAll(".options__stream");
    optionsActive.forEach((t) => {
      t.classList.add("d-none");
    });
  }
}

export default WatchVideo;
