import React, { Component } from "react";
import TimeAgoReact from 'timeago-react';
import Pagination from "react-js-pagination";
import LoadingImg from "./LoadingImg";
import {register, format} from 'timeago.js';

const skeletor = () => {
  let vecArr = [];
  for(let i = 0; i < 30; i++) {
    vecArr.push({
      number: i
    });
  }
  return vecArr;
}

function capitalizarPrimeraLetra(str) {
  return str.charAt(0).toUpperCase() + str.slice(1);
}

const serverDomain = 'https://server-cotiplustv.herokuapp.com';

// const serverDomain = 'http://localhost:3001';

class Movies extends Component {

  constructor(props) {
    super(props);
    this.state = {
      movies: [],
      numberPages: 0,
      page: 0,
      perPage: 30,
      loading: true,
      skeletor : skeletor(),
      search: '',
      keyword: ''
    };
  }
 
  handlePageChange(pageNumber) {
    console.log(`active page is ${pageNumber}`);
    if(this.state.keyword == '') {
      window.location.href = `/${this.props.type}?page=${pageNumber}`
    } else {
      window.location.href = `/${this.props.type}?keyword=${this.state.keyword}&page=${pageNumber}`
    }
    this.setState({page: pageNumber});
  }


  async componentDidMount() {
    const queryString = window.location.search;
    const urlParams = new URLSearchParams(queryString);
    const pageInit = urlParams.get('page') || 1;
    let response;
    let keyword = urlParams.get('keyword') || '';
    let titlePageNew = document.querySelector('title');
    if(this.props.type != '') {
      if(this.props.type == 'search') {
        titlePageNew.textContent = `Estas buscando: ${keyword}` + ' - ' + titlePageNew.textContent;
      } else {
        titlePageNew.textContent = titlePageNew.textContent + ' - ' + capitalizarPrimeraLetra(this.props.type);
      }
    }
    if(this.props.type == 'search') {
       response = await fetch(`${serverDomain}/${this.props.type}?keyword=${keyword}&page=${pageInit}`);
       this.setState({
        search: `Estas buscando: ${keyword}`
       });
    } else {
      response = await fetch(`${serverDomain}/${this.props.type}?page=${pageInit}`);
    }
    
    let data = await response.json();
    let page = data.page;
    let numberPages = 0;
    numberPages = data.numberPages;
    const localeFunc = (number, index, totalSec) => {
      // number: the timeago / timein number;
      // index: the index of array below;
      // totalSec: total seconds between date to be formatted and today's date;
      return [
        ['justo ahora', 'justo ahora'],
        ['%s segundos antes', 'en %s segundos'],
        ['1 minuto antes', 'en 1 minuto'],
        ['%s minutos antes', 'en %s minutos'],
        ['1 hora antes', 'en 1 hora'],
        ['%s horas antes', 'en %s horas'],
        ['1 día antes', 'en 1 día'],
        ['%s días antes', 'en %s días'],
        ['1 semana antes', 'en 1 semana'],
        ['%s semanas antes', 'en %s semanas'],
        ['1 mes antes', 'en 1 mes'],
        ['%s meses antes', 'en %s meses'],
        ['1 año antes', 'en 1 año'],
        ['%s años antes', 'en %s años']
      ][index];
    };
    // register your locale with timeago
    register('my-locale', localeFunc);
    // TimeAgo.addDefaultLocale({
    //   locale: 'es',
    //   now: {
    //     now: {
    //       current: "ahora",
    //       future: "hace un momento",
    //       past: "justo ahora"
    //     }
    //   },
    //   long: {
    //     year: {
    //       past: {
    //         one: "{0} año atrás",
    //         other: "{0} años atrás"
    //       },
    //       future: {
    //         one: "en {0} año",
    //         other: "en {0} años"
    //       }
    //     },
    //     month: {
    //       past: {
    //         one: "{0} mes atrás",
    //         other: "{0} meses atrás"
    //       },
    //       future: {
    //         one: "en {0} més",
    //         other: "en {0} meses"
    //       }
    //     },
    //     day: {
    //       past: {
    //         one: "{0} día atrás",
    //         other: "{0} días atrás"
    //       },
    //       future: {
    //         one: "en {0} día",
    //         other: "en {0} días"
    //       }
    //     }
    //   }
    // })
    // if(this.props.type == 'peliculas') {
    //   numberPages = 332;
    // } else if(this.props.type == 'series') {
    //   numberPages = 89;
    // } else if(this.props.type == '') {
    //   numberPages = 419;
    // } else if(this.props.type == 'temporadas') {
    //   numberPages = 419;
    // } else if(this.props.type == 'popular') {
    //   numberPages = 419;
    // } else if(this.props.type == 'search') {
    //   numberPages = data.numberPages;
    // }
    console.log(numberPages);
    this.setState({ movies: data.data, numberPages, page: parseInt(page), loading: false, keyword });
    LoadingImg();
  }

  render() {
    return (
      <div className="content__body">
        <div className="content_last_videos">
          <h3 className="title_videos_last">{this.state.search != '' ? this.state.search : this.props.content}</h3>
          <div className="content_img_videos peliculas__content animate__animated animate__fadeIn">
            {
              this.state.loading ? <div className="skeletor-preview-video">
                { 
                  Array.from({length: 30}, (_, i) => i + 1).map(() => {
                    return <div class="skeletor-preview-content">
                    <div class="skeletor-img-preview"></div>
                    <div class="line line-1"></div>
                    <div class="line line-3"></div>
                  </div>

                  })
                }
            </div>
                 : this.state.movies.length <= 0 ? <div class="not__found">No se encontraron resultados.</div> : this.state.movies.map((movie) => {
                return (
                  <a
                    href={movie.targetWatch}
                    className="carousel__elemento wave tooltip_auto"
                    data-tippy-content={movie.title}
                    data-video-id=""
                    key={movie.targetWatch}
                  >
                    <img
                      className="image-loading"
                      src={this.props.imagePreloader}
                      alt=""
                      data-src={movie.image}
                    />
                    <p className="title-video">{movie.title}</p>
                    <div className="history__date">
                      <label className="date">{movie.date.includes(':') ? format(movie.date, 'my-locale') : movie.date}</label>
                    </div>
                  </a>
                );
              })
            }
            
          </div>
        </div>
        {
          !this.state.loading ? this.state.movies.length <= 0 ? '' : <Pagination
          activePage={this.state.page}
          itemsCountPerPage={this.state.perPage}
          totalItemsCount={this.state.numberPages * this.state.perPage}
          pageRangeDisplayed={5}
          onChange={this.handlePageChange.bind(this)}
        /> : ''
        }
      </div>
    );
  }
}

export default Movies;
