import React, { Component } from "react";

class Sidebar extends Component {
  state = {
    movies: {},
  };

  componentDidMount() {
    if(window.innerWidth <= 568) {
      window.document.addEventListener('click', (e) => {
        if(!e.target.closest('.sidebar') && !e.target.closest('.menu__bars')) {
          let sidebar = document.querySelector('.sidebar');
          let blockContainer = document.querySelector('.block__container__sidebar');
          sidebar.style.display = "none";
          blockContainer.classList.add('d-none');
        }
      });
    }
  }

  render() {
    return (
      <div className="sidebar sidebar__content animate__animated animate__fadeIn">
        <div className="indicator__navegation">Navegación</div>
        <div className="list__menu__options">
          <a
            href="/"
            className={this.props.selected == 'Home' ? 'option__active' : ''}
          >
            <i className="fas fa-home" aria-hidden="true"></i>Inicio
          </a>{" "}
          <a
            href="/peliculas"
            className={this.props.selected == 'Peliculas' ? 'option__active' : ''}
          >
            <i className="fas fa-laptop" aria-hidden="true"></i>Peliculas
          </a>{" "}
          <a href="/series" className={this.props.selected == 'Series' ? 'option__active' : ''}>
            <i className="fas fa-video" aria-hidden="true"></i>Series
          </a>{" "}
          <a href="/temporadas" className={this.props.selected == 'Temporadas' ? 'option__active' : ''}>
            <i className="fas fa-list-ul" aria-hidden="true"></i>Temporadas
          </a>{" "}
          <a href="/popular" className={this.props.selected == 'Popular' ? 'option__active' : ''}>
            <i className="fas fa-fire-alt"></i>Popular
          </a>{" "}
        </div>
      </div>
    );
  }
}

export default Sidebar;
