import React, { Component } from "react";

class Navbar extends Component {
  state = {
    movies: {},
  };

  render() {
    return (
      <footer className="content-footer">
        <div className="footer">
          <div className="copyright">
            {" "}
            <span>
              Copyright © 2021 <b className="text-dark">{this.props.title}</b>. All rights
              reserved.
            </span>{" "}
            <span className="go-right">
              {" "}
              <a
                href="/terminos"
                className="text-gray m-r-15"
              >
                Terminos y condiciones
              </a>{" "}
              <a
                href="/politicas"
                className="text-gray m-r-15"
              >
                Privacy &amp; Policy
              </a>{" "}
              <a
                href="#"
                className="text-gray"
              >
                Contacto
              </a>{" "}
            </span>
          </div>
        </div>{" "}
      </footer>
    );
  }
}

export default Navbar;
