import React, { Component } from "react";

import Sidebar from './Sidebar';

class Navbar extends Component {

  state = {
    movies: {},
  };

  componentDidMount() {
    if(window.innerWidth <= 568) {
      let sidebar = document.querySelector('.sidebar');
      sidebar.style.display = "none";
    }
  }

  render() {
    return (
      <div className="content__menu">
        <div className="navbar nav__home">
          <div className="content__bar__menu">
            <div className="menu__bars" onClick={this.toggleSidenav.bind(this)}>
              
              <svg
                viewBox="0 0 24 24"
                preserveAspectRatio="xMidYMid meet"
                focusable="false"
                className="style-scope yt-icon"
              >
                
                <g className="style-scope yt-icon">
                  
                  <path
                    d="M3 18h18v-2H3v2zm0-5h18v-2H3v2zm0-7v2h18V6H3z"
                    className="style-scope yt-icon"
                  ></path>
                </g>
              </svg>
            </div>
            <a
              className="title__logo__navbar"
              href="/"
            >
              {this.props.title}
            </a>
          </div>

          <div className="form__search">
            <input type="text" placeholder="Buscar" className="input__search" onKeyPress={this.searchMovie.bind(this)}/>
            <div className="search__icon" onClick={this.searchMovieClick.bind(this)}>
              
              <i className="fas fa-search" aria-hidden="true"></i>
            </div>
          </div>
        </div>
        <Sidebar selected={this.props.selected}/>
      </div>
    );
  }
 toggleSidenav() {
  // var node = ReactDOM.findDOMNode(this.refs.sidebar);
  // if (node.style.display === "none" || node.classList.contains('hidden')) {
  //   node.style.display = "block";
  //   node.classList.remove('hidden');
  // } else {
  //   node.style.display = "none";
  //   node.classList.add('hidden');
  // }
  let sidebar = document.querySelector('.sidebar');
  let blockContainer = document.querySelector('.block__container__sidebar');
  if (sidebar.style.display === "none" && sidebar.style.display) {
    sidebar.style.display = "block";
    let container = document.querySelector('.container');
    container.classList.remove('extend-block-left');
    let footer = document.querySelector('.content-footer');
    footer.classList.remove('extend-block-left');
    if(window.innerWidth <= 568) {
          blockContainer.classList.remove('d-none');

    }
  } else {
    sidebar.style.display = "none";
    let container = document.querySelector('.container');
    container.classList.add('extend-block-left');
    let footer = document.querySelector('.content-footer');
    footer.classList.add('extend-block-left');
    if(window.innerWidth <= 568) {
      blockContainer.classList.add('d-none');

    }

  }
  
  
 }
 searchMovie(e) {
  if(e.charCode == 13) {
    e.preventDefault();
    window.location.href = `${window.location.origin}/search?keyword=${e.currentTarget.value}`;
  }
 }
 searchMovieClick() {
  let inputSearch = window.document.querySelector('.input__search');
  window.location.href = `${window.location.origin}/search?keyword=${inputSearch.value}`;
 }
}

export default Navbar;
