import React from "react";
import { BrowserRouter as Router, Route, Redirect, Switch } from "react-router-dom";
import "./App.css";

import Navbar from "./components/Navbar";
import Movies from "./components/Movies";
import Footer from "./components/Footer";
import WatchVideo from "./components/WatchVideo";
import WatchSeasons from "./components/WatchSeasons";
import my404 from "./components/my404";

const imagePreloader = "/placeholder-image.png";
const title = 'CotiPlusTV';

function App() {
  return (
    <div className="main-app">
      <div className="block__container__sidebar d-none"></div>
        <Router>
        <Switch>
        <Route
            exact
            path="/"
            render={() => {
              return (
                <div className="app">
                  <Navbar title={title} selected="Home"/>
                  <div className="container">
                    <Movies type="" imagePreloader={imagePreloader} content="Agregados recientemente"/>
                  </div>
                  <Footer title={title}/>
                </div>
              );
            }}
          ></Route>
          <Route
            exact
            path="/peliculas"
            render={() => {
              return (
                <div className="app">
                  <Navbar title={title} selected="Peliculas"/>
                  <div className="container">
                    <Movies type="peliculas" imagePreloader={imagePreloader} content="Lista de Películas"/>
                  </div>
                  <Footer title={title}/>
                </div>
              );
            }}
          ></Route>
          <Route
            exact
            path="/series"
            render={() => {
              return (
                <div className="app">
                  <Navbar title={title} selected="Series"/>
                  <div className="container">
                    <Movies type="series" imagePreloader={imagePreloader} content="Lista de Series"/>
                  </div>
                  <Footer title={title}/>
                </div>
              );
            }}
          ></Route>
          <Route
            exact
            path="/temporadas"
            render={() => {
              return (
                <div className="app">
                  <Navbar title={title} selected="Temporadas"/>
                  <div className="container">
                    <Movies type="temporadas" imagePreloader={imagePreloader} content="Lista de temporadas"/>
                  </div>
                  <Footer title={title}/>
                </div>
              );
            }}
          ></Route>
          <Route
            exact
            path="/popular"
            render={() => {
              return (
                <div className="app">
                  <Navbar title={title} selected="Popular"/>
                  <div className="container">
                    <Movies type="popular" imagePreloader={imagePreloader} content="Más populares"/>
                  </div>
                  <Footer title={title}/>
                </div>
              );
            }}
          ></Route>
          <Route
            path="/ver/:title"
            exact
            render={() => {
              return (
                <div className="app">
                  <Navbar title={title} selected=""/>
                  <div className="container">
                    <WatchVideo imagePreloader={imagePreloader}/>
                  </div>
                  <Footer title={title}/>
                </div>
              );
            }}
          ></Route>
          <Route
            path="/ver/:title/temporadas"
            exact
            render={() => {
              return (
                <div className="app">
                  <Navbar title={title} selected=""/>
                  <div className="container">
                    <WatchSeasons imagePreloader={imagePreloader}/>
                  </div>
                  <Footer title={title}/>
                </div>
              );
            }}
          ></Route>
          <Route
            path="/search"
            render={() => {
              return (
                <div className="app">
                  <Navbar title={title} selected=""/>
                  <div className="container">
                    <Movies type="search" imagePreloader={imagePreloader} content="Estas buscando"/>
                  </div>
                  <Footer title={title}/>
                </div>
              );
            }}
          ></Route>
          <Route path='/404' component={my404} />
          <Route path="*" component={my404} />
           {/* <Redirect to='/404' /> */}
        </Switch>

          
        </Router>
        </div>
  );
}

export default App;
