import React, { Component } from "react";
import {register, format} from 'timeago.js';

import LoadingImg from "./LoadingImg";

function removeDuplicates(originalArray, prop) {
  var newArray = [];
  var lookupObject = {};

  for (var i in originalArray) {
    lookupObject[originalArray[i][prop]] = originalArray[i];
  }

  for (i in lookupObject) {
    newArray.push(lookupObject[i]);
  }
  return newArray;
}

function showDisqus() {
  var d = document,
    s = d.createElement("script");
  s.src = "https://cotiplustv.disqus.com/embed.js";
  s.setAttribute("data-timestamp", +new Date());
  (d.head || d.body).appendChild(s);
}

const serverDomain = "https://server-cotiplustv.herokuapp.com";

// const serverDomain = 'http://localhost:3001';


class WatchSeasons extends Component {
  state = {
    videoInfo: {},
    videosWatch: [],
    videosRelevant: [],
    loading: true,
    newImage: "",
    video: "",
    languagesVideo: [],
    seasons: [],
    seasonsProx: -1,
    error: false,
    prevEpisode: "",
    nextEpisode: "",
  };

  async componentDidMount() {
    const queryString = window.location.search;
    let titleSearch = window.location.href.split("/")[4].split('?')[0];
    // console.log(titleSearch);
    const urlParams = new URLSearchParams(queryString);
    const pageInit = urlParams.get("page") || 1;
    let response = await fetch(`${serverDomain}/ver/${titleSearch}/temporadas`);
    let data = await response.json();
    const localeFunc = (number, index, totalSec) => {
      // number: the timeago / timein number;
      // index: the index of array below;
      // totalSec: total seconds between date to be formatted and today's date;
      return [
        ['justo ahora', 'justo ahora'],
        ['%s segundos antes', 'en %s segundos'],
        ['1 minuto antes', 'en 1 minuto'],
        ['%s minutos antes', 'en %s minutos'],
        ['1 hora antes', 'en 1 hora'],
        ['%s horas antes', 'en %s horas'],
        ['1 día antes', 'en 1 día'],
        ['%s días antes', 'en %s días'],
        ['1 semana antes', 'en 1 semana'],
        ['%s semanas antes', 'en %s semanas'],
        ['1 mes antes', 'en 1 mes'],
        ['%s meses antes', 'en %s meses'],
        ['1 año antes', 'en 1 año'],
        ['%s años antes', 'en %s años']
      ][index];
    };
    // register your locale with timeago
    register('my-locale', localeFunc);
    let titlePageNew = document.querySelector("title");
    if (data.videoInfo) {
      titlePageNew.textContent =
        `Ver ${data.videoInfo.title}` + " | " + titlePageNew.textContent;
    }
    if (!data.error) {
      let newImage = data.videosWatch[0].image;
      let video;
      let seasons = removeDuplicates(data.videosWatch, "season");
      

      this.setState({
        videoInfo: data.videoInfo,
        videosWatch: data.videosWatch,
        videosRelevant: data.videosRelevant,
        loading: false,
        newImage,
        video,
        seasons,

      });
      LoadingImg();
      window.document.addEventListener("click", (e) => {
        if (!e.target.closest(".language__item")) {
          let selectedsActive = document.querySelectorAll(".options__stream");
          selectedsActive.forEach((item) => {
            item.classList.add("d-none");
            item.parentElement.querySelector(
              ".material-icons"
            ).innerHTML = `<i class="fas fa-angle-down"></i>`;
          });
        }
      });
      showDisqus();
    } else {
      this.setState({ error: true, loading: false });
    }
  }

  render() {
    return (
      <div className="watch__video__content">
        {this.state.loading ? (
          <div className="loading-page-skeletor">
            <div class="skeletor-video">
              <div class="skeletor-img">
                <div class="background-image"></div>
              </div>
              <div class="skeletor-info">
                <div class="line line-1"></div>
                <div class="line line-2"></div>
                <div class="line line-3"></div>
              </div>
            </div>
            <div class="skeletor-temps-serie">
              <div class="skeletor-container-temps">
              { 
                  Array.from({length: 3}, (_, i) => i + 1).map(() => {
                    return <div class="skeletor-temps"></div>
                  })
                }
              </div>
            </div>
          </div>
        ) : this.state.error ? (
          <div id="notfound">
            <div class="notfound">
              <div class="notfound-404">
                <h1>404</h1>
              </div>
              <h2>¡UPS! No se pudo encontrar esta página</h2>
              <p>
                Lo sentimos, pero la página que está buscando no existe, se ha
                eliminado. nombre cambiado o no está disponible temporalmente
              </p>
              <a href="/">Ir a la página de inicio</a>
            </div>
          </div>
        ) : (
          <div>
            <div className="card__preview">
              <div className="card__video">
                <div className="img__video__stream">
                  <img
                    className="image-loading"
                    src={this.props.imagePreloader}
                    alt=""
                    data-src={this.state.newImage}
                  />
                </div>
                <div className="card__info">
                  <div className="title__video">
                    {`${this.state.videoInfo.titleEpisode}`}
                  </div>
                  <div className="title__original">
                    @ {this.state.videoInfo.title}
                  </div>
                  <div className="title__indicator__description">Sinopsis:</div>
                  <div className="description__video">
                    {this.state.videoInfo.description}
                  </div>
                  <div className="country__video">
                    <div className="indicator">País:</div>
                  </div>
                  <div className="release__date__video">
                    <div className="indicator">Fecha:</div>
                  </div>
                </div>
              </div>
            </div>
            <div className="video-content">
                <div className="temps__serie">
                  {this.state.seasons.map((temp) => {
                    return (
                      <div className="temp__content">
                        {temp.season == this.state.videoInfo.season ? (
                          <div
                            className="temporada__number selected__temp"
                            id={`temporada${temp.season}`}
                            onClick={this.changeSeason.bind(this)}
                          >
                            Temporada {parseInt(temp.season)}
                          </div>
                        ) : (
                          <div
                            className="temporada__number"
                            id={`temporada${temp.season}`}
                            onClick={this.changeSeason.bind(this)}
                          >
                            Temporada {parseInt(temp.season)}
                          </div>
                        )}
                        <div
                          className={
                            temp.season == this.state.videoInfo.season
                              ? "temp__episodes"
                              : "temp__episodes d-none"
                          }
                        >
                          {this.state.videosWatch.map((episode) => {
                            return episode.season == temp.season ? (
                              episode.season == this.state.videoInfo.season &&
                              episode.episode ==
                                this.state.videoInfo.episode ? (
                                <a
                                  className="episode__content selected__episode"
                                  href={episode.targetWatch}
                                >
                                  <div className="img__episode">
                                    <img
                                      src={episode.image}
                                      className="image-loading"
                                      alt={episode.title}
                                    ></img>
                                  </div>
                                  <div className="number__episode">
                                    {episode.season} - {episode.episode}
                                  </div>
                                  <div className="info__episode">
                                    <div className="title__episode">
                                      {episode.title}
                                    </div>
                                    <div className="date__episode">
                                      {episode.date.includes(':') ? format(episode.date, 'my-locale') : episode.date}
                                    </div>
                                  </div>
                                </a>
                              ) : (
                                <a
                                  className="episode__content"
                                  href={episode.targetWatch}
                                >
                                  <div className="img__episode">
                                    <img
                                      src={episode.image}
                                      className="image-loading"
                                      alt={episode.title}
                                    ></img>
                                  </div>
                                  <div className="number__episode">
                                    {episode.season} - {episode.episode}
                                  </div>
                                  <div className="info__episode">
                                    <div className="title__episode">
                                      {episode.title}
                                    </div>
                                    <div className="date__episode">
                                      {episode.date.includes(':') ? format(episode.date, 'my-locale') : episode.date}
                                    </div>
                                  </div>
                                </a>
                              )
                            ) : (
                              ""
                            );
                          })}
                        </div>
                      </div>
                    );
                  })}
                </div>
            </div>
            <div id="disqus_thread"></div>
            <div className="video-info-extend">
              <h3 className="indicator__extend__info title_videos_last">
                Últimos estrenos
              </h3>
              <div className="content_img_videos animate__animated animate__fadeIn">
                {this.state.videosRelevant.map((movie) => {
                  return (
                    <a
                      href={movie.targetWatch}
                      className="carousel__elemento wave tooltip_auto"
                      data-tippy-content={movie.title}
                      data-video-id=""
                      key={movie.targetWatch}
                    >
                      <img
                        className="image-loading"
                        src={this.props.imagePreloader}
                        alt=""
                        data-src={movie.image}
                      />
                      <p className="title-video">{movie.title}</p>
                      <div className="history__date">
                        <label className="date">{movie.date.includes(':') ? format(movie.date, 'my-locale') : movie.date}</label>
                      </div>
                    </a>
                  );
                })}
              </div>
            </div>
            <a class="more__videos__content">
              {" "}
              <a class="btnMoreVideos wave" href="/">
                Ver más
              </a>{" "}
            </a>
          </div>
        )}
      </div>
    );
  }
  
  changeSeason(e) {
    let thisTarget = e.currentTarget;
    let elementTarget = e.currentTarget.parentElement.querySelector(
      ".temp__episodes"
    );
    let targetAll = document.querySelectorAll(".temp__episodes");
    targetAll.forEach((item) => {
      if (item != elementTarget) {
        item.classList.add("d-none");
      } else {
        item.classList.toggle("d-none");
      }
    });
    let toggleActive = false;
    if (thisTarget.classList.contains("selected__temp")) {
      toggleActive = true;
    }
    let selectedPrev = document.querySelector(".selected__temp");
    if (!toggleActive) {
      if (selectedPrev) {
        selectedPrev.classList.remove("selected__temp");
      }
      thisTarget.classList.add("selected__temp");
      window.scroll({
        top: thisTarget.offsetTop,
        behavior: "smooth",
      });
    } else {
      thisTarget.classList.toggle("selected__temp");
    }
  }
}

export default WatchSeasons;
